import React, { Component, Fragment } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Subscribe from 'assets/images/InfoPage/Subscribe.png';

// import appstore and playstore logo
import AppStoreBadge from 'assets/images/app-store-badge.png';
import PlayStoreBadge from 'assets/images/google-play-badge.png';

// Footer
import FormLandingFooter from '../../Templates/FormLanding/FormLandingFooter';

import './InfoPage.scss';

class InfoPage extends Component {
  componentDidMount() {
    window.scroll(0, 0);
  }

  renderSubscribe() {
    return (
      <Row className="subscribe-container" noGutters>
        <Col className="d-none d-sm-flex" xs={12} sm={5}>
          <img className="subscribe-main-img" src={Subscribe} alt="subscribe-img.svg" />
        </Col>
        <Col className="right-container" xs={12} sm={7}>
          <h3>Download our free app</h3>
          <h5>Get covered on the go, manage all your insurance matters in a single app.</h5>
          <Row noGutters>
            <Col className="d-flex d-lg-block justify-content-center" xs={6}>
              <a href="https://itunes.apple.com/sg/app/policypal/id1118511972?mt=8">
                <img
                  src={AppStoreBadge}
                  alt="appstore.jpg"
                  className="store-btn"
                  // effect="opacity"
                />
              </a>
            </Col>
            <Col className="d-flex d-lg-block justify-content-center" xs={6}>
              <a href="https://play.google.com/store/apps/details?id=com.idvsgjo31w9m05f6raeb3&amp;hl=en">
                <img
                  src={PlayStoreBadge}
                  alt="playstore.jpg"
                  className="store-btn"
                  // effect="opacity"
                />
              </a>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }

  renderBackGround() {
    const { upperBG } = this.props;

    return (
      <Fragment>
        {upperBG && <div className="circle-shape circle-upper" />}
        <div className="circle-shape circle-bottom" />
        {upperBG && <div className="circle-green" />}
      </Fragment>
    );
  }

  renderSubscribeSection() {
    const { noFooter, subscribeOverwrite } = this.props;

    if (!noFooter) {
      if (subscribeOverwrite) {
        return subscribeOverwrite;
      }
      return (
        <Col xs={12}>
          {subscribeOverwrite || this.renderSubscribe()}
        </Col>
      );
    }
    return null;
  }

  render() {
    const {
      className,
      noBg,
      noPolicyPal,
      noFooter,
      noAutoMargin,
      mode,
    } = this.props;

    const marginStyle = noAutoMargin && 'no-margin';

    return (
      <div className="position-relative">
        <Row className={`info-page ${className}`} noGutters>
          <Col xs={12} className={['landing-form-outer-container-new', marginStyle].join(' ')}>
            {
              !noBg ? this.renderBackGround() : null
            }
            <Row noGutters>
              { this.props.children }
              { this.renderSubscribeSection() }
            </Row>
          </Col>
        </Row>
        {
          !noFooter ?
            <FormLandingFooter
              big
              noPolicyPal={noPolicyPal}
              mode={mode}
            />
          : null
        }
      </div>
    );
  }
}

InfoPage.defaultProps = {
  className: '',
  noBg: false,
  noPolicyPal: false,
  noFooter: false,
  subscribeOverwrite: null,
  noAutoMargin: false,
  mode: 'short',
  upperBG: false,
};
InfoPage.propTypes = {
  className: PropTypes.string,
  mode: PropTypes.string,
  noBg: PropTypes.bool,
  noPolicyPal: PropTypes.bool,
  noFooter: PropTypes.bool,
  subscribeOverwrite: PropTypes.object,
  noAutoMargin: PropTypes.bool,
  upperBG: PropTypes.bool,
};

export default InfoPage;
